.inventory {
    border: solid;
    height: 100%;
}

.inventory-contents {
    display: grid;
    margin: 1rem;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(7, 1fr);
    grid-gap: 1rem;
}

.item-card{
    text-align :center;
    border: solid;
    grid-gap: 8px 8px;
    user-select: none;
}

.item-card:active{
    background: yellowgreen;
    font-weight: bolder;
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.2);
    transform: translateY(2px);
}

.item-id{
    display: none;
}

.item-name{
    margin-top: 0.5rem;
    font-size: 2rem;
}
.item-scale{
    font-size: 0.8rem;
    height: 1rem;
}
.item-price{
    margin-bottom: 0.5rem;
}