.monitor {
    grid-area: monitor;
    position: relative;
}

.orderItem{
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: repeat(10, 1fr);
}

.orderRow{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    font-size: 1.2rem;
}

.totalPrice{
    position: absolute;
    font-size: 1.5rem;
    font-weight: bold;
    right: 0.5rem;
    bottom: 0.5rem;
}

.bold{
    font-weight: bolder;
}

.main-mark{
    text-align: center;
    font-size: 4rem;
    font-weight: bold;
}

.main-message{
    text-align: center;
    font-size: 1.5rem;
}

.message-row{
    margin: 0.8rem;
}

.status{
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 4rem;
    font-weight: bold;
    animation: fadeOut 0.8s 0s forwards;
}

@keyframes fadeOut {
    from { opacity: 1; }
    to { opacity: 0; }
}