.App {
    width: 100vw;
    height: 100vh;
    display: grid;
    grid-template-columns: 2fr 3fr;
    grid-template-rows: 1fr 4fr;
    grid-template-areas:
        "submitter submitter"
        "calculator inventory";
    margin: 0px;
}
span{
    margin: 4px;
}
.submitter{
    grid-area: submitter;
}

.calculator {
    border: solid;
    display: grid;
    grid-template-rows: 1fr 2fr;
    grid-template-areas:
        "monitor"
        "price-selector";
}

.border {
    border: solid;
}

h2{
    margin: 1rem;
}