.price-selector{
    border: solid;
    grid-area: price-selector
}

.price-selector-container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;

}

.price-card {
    text-align: center;
    font-weight: bold;
    font-size: 2rem;
    margin: 0.5rem;
    padding: 0.7rem;
    border: solid;
    width: 80%;
}

.price-card:active{
    background: yellowgreen;
    font-weight: bolder;
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.2);
    transform: translateY(2px);
}